











import { Component, Vue } from "vue-property-decorator";
import { mapGetters } from "vuex";

@Component({
    computed: {
        ...mapGetters({
            current: 'weather/current_station',
            weather: 'weather/current_weather'
        })
    }
})
export default class WeatherLastData extends Vue {
    public current! : any;
    public weather!: any;
}
